import userIcon from "../public/userIcon.vue";
import cookies from "vue-cookies";
export default {
  components: { userIcon },
  props: {
    food: {
      type: Object,
    },
  },
  data() {
    return {
      grade: this.food.grade,
    };
  },
  methods: {
    async rate(data) {
      console.log(data);
      console.log(this.grade);
      if (this.grade == 0) {
        console.log("a");
        await this.$axios({
          url: "/api/grade",
          method: "post",
          data: {
            food_id: this.food.food_id,
            token: cookies.get("token"),
            grade: data,
          },
        })
          .then((res) => {
            console.log(res.data);
            this.grade = data;
          })
          .catch((err) => {
            alert(err);
          });
      } else {
        console.log("b");
        await this.$axios({
          url: "/api/grade",
          method: "patch",
          data: {
            food_id: this.food.food_id,
            token: cookies.get("token"),
            grade: data,
          },
        })
          .then((res) => {
            console.log(res.data);
            this.grade = res.data;
          })
          .catch((err) => {
            alert(err);
          });
      }
    },
  },
  mounted() {},
};
